'use client';

/**
 * Home / Landing page – Crafting Corner
 * – Full animated hero
 * – Responsive Categories showcase (pulled from static JSON)
 * – Featured products carousel style grid
 */

import { motion } from 'framer-motion';
import Image from 'next/image';
import Link from 'next/link';

import Hero from '../components/Hero';
import ProductCard from '../components/ProductCard';

// Static JSON data (bundled at build-time only – no network calls!)
import categories from '../data/categories.json';

// Pick a handful of products to feature on the home page (could be replaced by an
// algorithm or a CMS flag later – for now, keep it static for performance / demo).
const featuredProducts = [
  {
    title: 'Oak Dining Table',
    price: 45_000,
    img: '/assets/img/products/oak-table.webp',
    slug: 'oak-dining-table',
  },
  {
    title: 'Walnut Armchair',
    price: 28_000,
    img: '/assets/img/products/walnut-chair.webp',
    slug: 'walnut-armchair',
  },
];

// ───────────────────────────────────────────────────────────────────────────────
// Tiny helper component for the category tiles
// ───────────────────────────────────────────────────────────────────────────────
function CategoryCard({ title, slug, image }: { title: string; slug: string; image: string }) {
  return (
    <motion.article
      whileHover={{ y: -6, scale: 1.02 }}
      transition={{ duration: 0.25, ease: 'easeOut' }}
      className="group relative overflow-hidden rounded-2xl shadow-lg"
    >
      <Link href={`/${slug}`} className="block h-full w-full">
        {/* Cover photo */}
        <Image
          src={image}
          alt={title}
          width={600}
          height={400}
          className="h-full w-full object-cover transition-transform duration-300 group-hover:scale-105"
          priority={false}
        />
        {/* Gradient overlay + title */}
        <div className="absolute inset-0 flex items-end bg-gradient-to-t from-black/60 to-black/0 p-4">
          <h3 className="font-display text-lg font-semibold text-ivory">{title}</h3>
        </div>
      </Link>
    </motion.article>
  );
}

// ───────────────────────────────────────────────────────────────────────────────
// Page component
// ───────────────────────────────────────────────────────────────────────────────
export default function Home() {
  return (
    <>
      {/* ——— Hero ——— */}
      <Hero />

      {/* ——— Categories showcase ——— */}
      <section className="mx-auto my-24 max-w-6xl px-4">
        <h2 className="mb-8 text-center font-display text-3xl font-semibold text-walnut md:text-4xl">
          Browse by Category
        </h2>

        <div className="grid grid-cols-2 gap-6 sm:grid-cols-3 lg:grid-cols-4">
          {(categories as { title: string; slug: string; image: string }[]).map((cat, i) => (
            <CategoryCard key={cat.slug} {...cat} />
          ))}
        </div>
      </section>

      {/* ——— Featured products ——— */}
      <section className="mx-auto mb-32 max-w-6xl px-4">
        <h2 className="mb-8 text-center font-display text-3xl font-semibold text-walnut md:text-4xl">
          Hand-picked Favourites
        </h2>

        <div className="grid grid-cols-[repeat(auto-fill,minmax(260px,1fr))] gap-8">
          {featuredProducts.map((p) => (
            <ProductCard key={p.slug} {...p} />
          ))}
        </div>
      </section>
    </>
  );
}
